import React, { Component } from 'react'
import firebase from '../firebase'
import {
  Grid,
  Paper,
  TextField,
  Button,
  MenuItem,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions
} from '@material-ui/core'
import provinces from './province'

const comTypeSelect = [
  {
    value: 'government',
    label: ''
  },
  {
    value: 'enterprise',
    label: ''
  },
  {
    value: 'private',
    label: ''
  },
  {
    value: 'company',
    label: ''
  },
  {
    value: 'other',
    label: ' '
  },
]

export default class CompanyAdd extends Component {
  constructor(props) {
    super(props)
    this.state = {
      open: false,
      name: null,
      tel: '',
      address: '',
      address1: '',
      address2: '',
      province: '',
      zip: '',
      comType: '',
      objective: '',
      other: true,
      isNull: false
    }
  }

  componentDidMount() {
    document.title = ' - ACTLOG ADMIN'
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        //
      } else {
        this.props.history.push('/')
      }
    })
  }

  onChange = (e) => {
    const { name, value } = e.target
    this.setState({ [name]: value })
    if (value === 'other') {
      this.setState({ other: false })
    }
  }

  onSubmit = (e) => {
    e.preventDefault()
    const { name, tel, address, address1, address2, province, zip, comType, objective } = this.state
    if (name) {
      // var date = new Date()
      // var year = date.getFullYear()
      // var month = date.getMonth()
      // var day = date.getDay()
      // var hour = date.getHours()
      // var min = date.getMinutes()
      // var sec = date.getSeconds()
      // var id = `${year}-${month}-${day}-${hour}-${min}-${sec}`
      firebase.database().ref(`company`).push({
        name,
        tel,
        address,
        address1,
        address2,
        province,
        zip,
        comType,
        objective
      }).then(() => {
        this.props.history.goBack()
      })
    } else {
      this.setState({ isNull: true })
    }
  }

  handleAlert() {
    this.setState({ open: !this.state.open })
  }

  Alert() {
    const { open, isNull } = this.state
    if (isNull) {
      return (
        <Dialog
          open={open}
          onClose={this.handleAlert.bind(this)}>
          <DialogTitle>{``}</DialogTitle>
          <DialogContent>
            <DialogContentText style={{ color: 'red' }}>
              {` []`}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button
              color='primary'
              onClick={() => this.setState({ isNull: false, open: false })}>
              </Button>
          </DialogActions>
        </Dialog>
      )
    } else {
      return (
        <Dialog
          open={open}
          onClose={this.handleAlert.bind(this)}>
          <DialogTitle>{``}</DialogTitle>
          <DialogContent>
            <DialogContentText>
              {` ?`}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={this.handleAlert.bind(this)}>
              </Button>
            <Button
              color='primary'
              onClick={this.onSubmit.bind(this)}>
              </Button>
          </DialogActions>
        </Dialog>
      )
    }
  }

  render() {
    return (
      <Grid
        container
        justify='center'
        alignItems='center'>
        {this.Alert()}
        <Paper
          style={{ overflow: 'auto', marginTop: 20, width: 500, padding: 20 }}>
          <Grid>
            <TextField
              fullWidth
              label=''
              variant='outlined'
              margin='normal'
              name='name'
              onChange={this.onChange} />
          </Grid>
          <Grid>
            <TextField
              fullWidth
              select
              label=''
              name='comType'
              onChange={this.onChange}
              InputLabelProps={{ shrink: true }}
              margin='normal'
              variant='outlined'>
              {comTypeSelect.map((option, i) => (
                <MenuItem key={i} value={option.value}>{option.label}</MenuItem>
              ))}</TextField>
            <TextField
              disabled={this.state.other}
              fullWidth
              label=' '
              variant='outlined'
              margin='normal'
              name='comType'
              onChange={this.onChange} />
          </Grid>
          <Grid>
            <TextField
              multiline
              rows={6}
              fullWidth
              label=''
              variant='outlined'
              margin='normal'
              name='objective'
              onChange={this.onChange} />
          </Grid>
          <Grid>
            <TextField
              fullWidth
              label=''
              variant='outlined'
              margin='normal'
              name='tel'
              onChange={this.onChange} />
          </Grid>
          <Grid>
            <TextField
              multiline
              rows={6}
              fullWidth
              label=' /  / '
              variant='outlined'
              margin='normal'
              name='address'
              onChange={this.onChange} />
            <TextField
              fullWidth
              label=' / '
              variant='outlined'
              margin='normal'
              name='address1'
              onChange={this.onChange} />
            <TextField
              fullWidth
              label=' / '
              variant='outlined'
              margin='normal'
              name='address2'
              onChange={this.onChange} />
            <TextField
              fullWidth
              select
              label=''
              name='province'
              onChange={this.onChange}
              value={this.state.province}
              margin='normal'
              variant='outlined'>
              {provinces.map((option, i) => (
                <MenuItem key={i} value={option.name}>{option.name}</MenuItem>
              ))}</TextField>
            <TextField
              fullWidth
              label=''
              variant='outlined'
              margin='normal'
              name='zip'
              onChange={this.onChange} />
          </Grid>
          <Grid
            container
            direction='column'
            alignItems='center'>
            <Grid
              style={{ marginTop: 15 }}>
              <Button
                variant='contained'
                style={{ marginRight: 10 }}
                onClick={() => this.props.history.goBack()}>
                </Button>
              <Button
                variant='contained'
                color='primary'
                style={{ marginRight: 10 }}
                onClick={this.handleAlert.bind(this)}>
                </Button>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    )
  }
}